.DesktopNav {
    display: none;
}

.DesktopNav li, .SideDrawerNav li {
    list-style-type: none;
}

@media only screen and (min-width: 601px) {
    .DesktopNav {
        display: block;        
    }
    .DesktopNav li {
        display: inline;
        margin-right: 10px;
    }
}


